/*add css by rohit*/
#contact-form { position:relative; }
#contact-form h1 { padding-bottom: 15px; text-align: center; margin-bottom: 70px; position: relative; margin-top: 0px; text-transform: uppercase; color:#333;}
#contact-form h1:before { content: ""; display: inline-block; position: absolute; width: 150px; height: 3px; background:rgb(12, 120, 206); bottom: 0px; left: 50%; transform: translateX(-50%); -moz-transform: translateX(-50%); -webkit-transform: translateX(-50%); -ms-transform: translateX(-50%);  }
#contact-form input { border-radius: 5px; height: 40px; padding: 7px 10px; line-height: 26px;}
#contact-form select { border-radius: 5px !important; height: 40px !important; padding: 7px 20px 7px 10px !important; line-height: 26px;}
#contact-form button[type="submit"] { display: inline-block; min-width: 200px; float:right; height: 45px; border-radius: 10px; padding: 10px 15px; line-height: 25px; font-weight: 600; text-transform: uppercase;}
#contact-form .circle_edit { position: absolute; top: 30px; right: 20px; z-index: 9;}
